.CheckBox_checkbox__1bHbk input + label:before {
  content: '';
  display: inline-block;
  width: 1.6em;
  height: 1.6em;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid #adb5bd;
  border-radius: 50%;
